<template>
    <div class="base">
      <div class="header">
        <div class="title">
          <div class="back" @click="putStatus"><Icon type="ios-arrow-dropleft-circle" /><span>back</span></div>
          <div class="header-title" v-text="base.header"></div>
        </div>
        <div class="infos">
          <div><Icon type="md-time" /><span v-text="base.times"></span></div>
          <div><Icon type="md-chatboxes" /><span>{{base.comment.length}}评论</span></div>
          <div><Icon type="md-eye" /><span>{{base.read}}次阅读</span></div>
          <div><Icon type="md-thumbs-up" /><span>{{base.like}}点赞数</span></div>
        </div>
    </div>
      <div class="description"><p  v-text="base.text"></p></div>
      <div class="content">
        <div class="article" v-for="val in base.article">
          <h3 v-text="val.title"></h3>
          <p v-text="val.explanation"></p>
          <pre v-if="val.code!==''" v-text="val.code">
          </pre>
          <p v-text="val.note"></p>
        </div>
      </div>
      <div class="footer">
        <div class="command">
          <div class="like"><button><Icon type="md-thumbs-up" />点赞</button></div>
          <div class="comback" @click="putStatus">
            <Icon type="ios-arrow-dropleft-circle" /><span>back</span>
          </div>
        </div>
        <div class="comment">
          <!-- //评论区 -->
          <h2>评论区域</h2>
          <div class="says" v-for="say in base.comment_content">
            <p><Icon type="ios-person" />{{say.user}}: {{say.text}}</p>
            <div class="reply-content">
              <p v-for="reply in say.reply_comment"><Icon type="ios-person" />{{reply.user}}: {{reply.text}}</p>
            </div>
            <div class="reply">
              <button>回复</button>
            </div>
          </div>
        </div>
        <div class="send-comment">
          <h2>发表评论</h2>
          <div class="input-text">
            <textarea></textarea>
          </div>
          <Button @click="put">发表评论</Button>
        </div>
      </div>
    </div>
</template>

<script>
    export default {
      name: "showarticle",
      data(){
        return{
          base: this.showdata,
          url:"http://120.79.233.5:6060"
        }
      },
      props:['showdata'],
      mounted(){
      },
      methods:{
        putStatus(){//用于返回到父组件
          this.$emit(this.showdata.change, false)
        },
        put(){
          if(!this.base.login){
            this.$Message.error({
              content: '请先登入后再行评论!',
              duration: 8,
              closable: true
            });
          }
        },
      }
    }
</script>

<style scoped lang="less">

/*start less类*/
  .xflex(@num){
    flex: @num;
    -webkit-flex: @num;
    -ms-flex: @num;
    -moz-box-flex: @num;
  }
  .xdisplayflex(){
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex
  }
  .xboxshow(){
    -moz-box-shadow:1px 1px 5px #515151;
    -webkit-box-shadow:1px 1px 5px #515151;
    box-shadow:1px 1px 5px #515151;
  }
  .bgimg(@path){
    background-image: url(@path);
    -moz-background-size:cover; /* 老版本的 Firefox */
    background-repeat:no-repeat;
    background-size: cover;
    background-position: center center;
  }
  .h2(){
    h2{
      border-bottom: 1px solid rgba(83, 83, 83, 0.15);
    }
    h2::after{
      content: "";
      display: block;
      width: 26%;
      border: 1px solid rgb(0, 162, 255);
      vertical-align: middle;
    }
  }
  @color: rgb(179, 179, 179);
  @textcolor: #0097ee;
/*end less类*/

  @media (max-width: 750px) {// 移动端
    .base{
      width: 90%;
      margin: 0 auto;
      margin-bottom: 50/32rem;
      .header{
        margin-bottom: 20/32rem;
        .title{
          .xdisplayflex();
          .back{
            .xflex(1);
            .xdisplayflex();
            align-items: center;
            justify-content: center;
            font-size: 26/32rem;
            cursor: pointer;
          }
          .back:hover{
            color: @textcolor;
          }
          .header-title{
            .xflex(9);
            font-size: 23/32rem;
            font-weight: 700;
          }
        }
        .infos{
          .xdisplayflex();
          color: @color;
          align-items: center;
          justify-content: center;
          font-size: 15/32rem;
          div{
            margin: 0 10/32rem;
          }
        }
      }
      .description{
        margin-bottom: 20/32rem;
        text-align: left;
        font-size: 18/32rem;
        p{
          text-indent: 2em;
        }
      }
      .content{
        margin-bottom: 20/32rem;
        text-align: left;
        font-size: 16/32rem;
        .article{
          h3{
            color: #2baee9;
          }
          pre{
            border: 1/32rem solid #B3B3B3;
            border-radius: 5/32rem;
            padding: 8/32rem;
            overflow: auto;
            .xboxshow();
          }
          p{
            text-indent: 2em;
            color: #626b7d;
          }
        }
      }
      .footer{
        text-align: left;
        margin: 20/32rem 0;
        .command{
          margin-bottom: 20/32rem;
          .xdisplayflex();
          flex-direction: row;
          .like{
            .xflex(9);
            button{
              .xdisplayflex();
              align-items: center;
              justify-content: center;
              border: 1/32rem solid @textcolor;
              background: #fff;
              color:  @textcolor;
              width: 130/32rem;
              height: 36/32rem;
            }
            button:hover{
              background: @textcolor;
              color: #fff;
            }
          }
          .comback{
            .xflex(1);
            .xdisplayflex();
            align-items: center;
            justify-content: center;
            font-size: 18/32rem;
          }
          .comback:hover{
            color: @textcolor;
          }
        }
        .comment{
          h2{
            margin: 20/32rem 0;
          }
          .says{
            p{
              margin: 5/32rem 0;
              font-size: 18/32rem;
              color: @color;
              .xdisplayflex();
              align-items: center;
              i{
                font-size: 30/32rem;
              }
            }
            .reply-content{
              margin-left: 60/32rem;
              p{
                margin-bottom: 20/32rem;
              }
            }
            .reply{
              text-align: right;
              color: @color;
              button{
                border: 1/32rem solid @textcolor;
                background: #fff;
                color:  @textcolor;
                width: 130/32rem;
                height: 36/32rem;
                cursor: pointer;
              }
              button:hover{
                background: @textcolor;
                color: #fff;
              }
            }
          }

        }
        .send-comment{
          margin-bottom: 20/32rem;
          .input-text{
            margin: 20/32rem 0;
            textarea{
              height: 130/32rem;
              width: 100%;
              border: 1/32rem solid rgba(0,0,0,0.2);
              padding: 10/32rem 10/32rem;
              resize: none;
            }
          }
          button{
            width: 100%;
            height: 46/32rem;
            color: #ffffff;
            background: @textcolor;
            .xdisplayflex();
            align-items: center;
            justify-content: center;
          }
        }
      }
    }
  }
  @media (min-width: 750px){
    .base{
      .header{
        margin-bottom: 20px;
        .title{
          .xdisplayflex();
          .back{
            .xflex(1);
            .xdisplayflex();
            align-items: center;
            justify-content: center;
            font-size: 20px;
            cursor: pointer;
          }
          .back:hover{
            color: @textcolor;
          }
          .header-title{
            .xflex(9);
            font-size: 23px;
          }
        }
        .infos{
          .xdisplayflex();
          color: @color;
          align-items: center;
          justify-content: center;
          font-size: 15px;
          div{
            margin: 0 10px;
          }
        }
      }
      .description{
        margin-bottom: 20px;
        text-align: left;
        font-size: 18px;
        p{
          text-indent: 2em;
        }
      }
      .content{
        margin-bottom: 20px;
        text-align: left;
        font-size: 16px;
        .article{
          h3{
            color: #2baee9;
          }
          pre{
            border: 1px solid #B3B3B3;
            border-radius: 5px;
            padding: 8px;
            max-width: 800px;
            overflow: auto;
            .xboxshow();
          }
          p{
            text-indent: 2em;
            color: #626b7d;
          }
        }
      }
      .footer{
        text-align: left;
        margin: 20px 0;
        .command{
          margin-bottom: 30px;
          .xdisplayflex();
          .like{
            .xflex(9);
            button{
              .xdisplayflex();
              align-items: center;
              justify-content: center;
              border: 1px solid @textcolor;
              background: #fff;
              color:  @textcolor;
              width: 130px;
              height: 36px;
              cursor: pointer;
            }
            button:hover{
              background: @textcolor;
              color: #fff;
            }
          }
          .comback{
            .xflex(1);
            .xdisplayflex();
            align-items: center;
            justify-content: center;
            font-size: 18px;
            cursor: pointer;
          }
          .comback:hover{
            color: @textcolor;
          }
        }
        .comment{
          h2{
            margin: 20px 0;
          }
          .says{
            p{
              margin: 5px 0;
              font-size: 18px;
              color: @color;
              .xdisplayflex();
              align-items: center;
              i{
                font-size: 30px;
              }
            }
            .reply-content{
              margin-left: 60px;
              p{
                margin-bottom: 20px;
              }
            }
            .reply{
              text-align: right;
              color: @color;
              button{
                border: 1px solid @textcolor;
                background: #fff;
                color:  @textcolor;
                width: 130px;
                height: 36px;
                cursor: pointer;
              }
              button:hover{
                background: @textcolor;
                color: #fff;
              }
            }
          }

        }
        .send-comment{
          margin-bottom: 20px;
          .input-text{
            margin: 20px 0;
            textarea{
              height: 130px;
              width: 100%;
              border: 1px solid rgba(0,0,0,0.2);
              padding: 10px 10px;
              resize: none;
            }
          }
          button{
            width: 100%;
            height: 36px;
            color: #ffffff;
            background: @textcolor;
          }
        }
      }
    }
  }
</style>



