<template>
  <div class="home">
    <div class="index" v-if="!detailStart">
      <div class="home-title">
        <h2><Icon type="md-flame" />最新动态</h2>
      </div>
      <ul>
        <li v-for="(val, i) in show" v-show="i<count">
          <div class="content">
            <div class="imgs" >
              <img :src="val.img_addr|srcFilters" alt="img" @click="showArticle(val)">
            </div>
            <div class="text">
              <div class="text-top">
                <i>{{val.show_type|toUpper}}</i>
                <span v-text="val.header" @click="showArticle(val)"></span>
              </div>
              <div class="text-content" @click="showArticle(val)"><span v-text="val.text"></span></div>
            </div>
          </div>
          <div class="infos">
            <div class="infos-left" v-if="showNav">
              <div><Icon type="md-time" /><span v-text="val.times"></span></div>
              <div><Icon type="md-chatboxes" /><span>{{val.comment.length}}评论</span></div>
              <div><Icon type="md-eye" /><span>{{val.read}}次阅读</span></div>
              <div><Icon type="md-thumbs-up" /><span>{{val.like}}点赞数</span></div>
            </div>
            <div class="infos-left" v-if="!showNav">
              <!--适配手机宽度 避免内容多导致换行-->
              <div><Icon type="md-time" /><span v-text="val.times"></span></div>
              <div><Icon type="md-chatboxes" /><span>{{val.comment.length}}查看</span></div>
              <div><Icon type="md-eye" /><span>{{val.read}}阅读</span></div>
              <div><Icon type="md-thumbs-up" /><span>{{val.like}}点赞</span></div>
            </div>
            <div class="infos-right" @click="showArticle(val)">
              阅读全文<Icon type="ios-arrow-dropright-circle" />
            </div>
          </div>
        </li>
      </ul>
      <div class="more">
      <span @click="more">{{moreText}}
        <Icon type="ios-arrow-dropdown-circle" v-if="moreStatus"/>
      </span>
      </div>
    </div>
    <showarticle @homechangeStatus="changeStatus" ref="product" :showdata="detail" v-if="detailStart"></showarticle>
  </div>
</template>

<script>
  import showarticle from './showarticle';
export default {
  name: 'Home',
  data () {
    return {
      show:[ ],//通过get数据库 获取最新动态
      count:6, //默认展示6条数据 递增展示更多
      moreText:"阅读更多",
      moreStatus:true,
      login:this.outdata, //来自父组件
      detail:{}, // 展示数据 传递给子组件
      detailStart: false,
      showNav:false,
      url: "http://120.79.223.5:6060",
    }
  },
  components:{
    showarticle
  },
  props:['outdata'],
  created(){

  },
  filters:{
    srcFilters(value){
      return value.replace("/www/wwwroot/dqm.zixxxx.com/", "/");
    },
    toUpper(value){
      let n = value.length;
      let one = value[0].toUpperCase();
      return one + value.slice(1, n)
    },
  },
  mounted(){
    this.getWidth();
    this.getLatestNew();
  },
  methods:{
    more(){
      let num = this.show.length;
      if(!this.moreStatus){return}
      if(this.count >= num){
        this.moreText = "我是有底线的...";
        this.moreStatus = false
      }else{
        this.count = this.count + 3
      }
    },
    showArticle(data){
      this.detail = data;
      this.detail["login"] = this.login;
      this.detail["change"] = "homechangeStatus";
      this.detailStart = !this.detailStart;
      let obj = {
        "idNum" :data.id,
        "showType":data.show_type
      };
      this.$http.post(this.url + "/web/api/addRead", this.$qs.stringify(obj)).then(res=>{}, err=>{
        console.log(err)
      })
    },
    changeStatus(){
      this.detailStart = !this.detailStart
    },
    getWidth(){
      let w = window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth;
      if(w>750){
        this.showNav=true;
      }
    },
    getLatestNew(){
      let obj = {
        "collection" : "latestnew",
        "num":20
      };
      this.$http.defaults.headers['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8';  //此处是增加的代码，设置请求头的类型
      this.$http.post(this.url + "/base/api/getcmdb", this.$qs.stringify(obj)).then(recv=>{
        this.show = recv.data;
      }, err=>{
        this.show = [];
        this.$Notice.error({
          title: '通知消息',
          desc: "获取最新动态数据错误:"+ err.message
        });
      })
    },

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">

  /*start less类*/
  .xflex(@num){
    flex: @num;
    -webkit-flex: @num;
    -ms-flex: @num;
    -moz-box-flex: @num;
  }
  .xdisplayflex(){
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex
  }
  .xboxshow(){
    -moz-box-shadow:1px 1px 5px #515151;
    -webkit-box-shadow:1px 1px 5px #515151;
    box-shadow:1px 1px 5px #515151;
  }
  .bgimg(@path){
    background-image: url(@path);
    -moz-background-size:cover; /* 老版本的 Firefox */
    background-repeat:no-repeat;
    background-size: cover;
    background-position: center center;
  }
  .h2(){
    h2{
      border-bottom: 1px solid rgba(83, 83, 83, 0.15);
    }
    h2::after{
      content: "";
      display: block;
      width: 18%;
      border: 1px solid rgb(0, 162, 255);
      vertical-align: middle;
    }
  }
  @color: rgb(102, 102, 102);
  @textcolor: #0097ee;
  /*end less类*/
  @media (max-width: 750px) {// 移动端
    .home{
      width: 95%;
      margin: 0 auto;
      margin-top: 16/32rem;
      .index{
        .home-title{
          text-align: left;
          .h2()
        }
        ul{
          margin:20/32rem 0;
          li{
            margin-bottom: 26/32rem;
            .content{
              .xdisplayflex();
              flex-wrap: wrap;
              -webkit-flex-wrap: wrap;
              -ms-flex-wrap: wrap;
              margin-bottom: 10/32rem;
              .imgs{
                .xflex(3);
                margin-right: 16/32rem;
                img{
                  width: 240/32rem;
                  height: 145/32rem;
                }
              }
              .text{
                .xflex(7);
                text-align: left;
                .text-top{
                  //margin-bottom: 10/32rem;
                  i{
                    color: #fff;
                    font-size: 15/32rem;
                    background: @textcolor;
                    padding: 3/32rem 10/32rem;
                    -webkit-border-top-right-radius: 50%;
                    -webkit-border-bottom-right-radius: 50%;
                    border-top-right-radius:50%;
                    border-bottom-right-radius:50%;
                  }
                  span{
                    font-size: 16/32rem;
                    font-weight: 700;
                  }
                  span:hover{
                    color: @textcolor;
                    cursor: pointer;
                  }
                }
                .text-content{
                    span{
                        font-size:12/32rem;
                        color: @color;
                    }
                }
              }
            }
            .infos{
              .xdisplayflex();
              color: @color;
              font-size: 13/32rem;
              .infos-left{
                .xflex(8);
                .xdisplayflex();
                align-items: center;
                div{
                  .xdisplayflex();
                  align-items: center;
                  margin-right: 10/32rem;
                  i{
                    margin: 0 3/32rem;
                  }
                  i:first-child{
                    margin-left: 0;
                  }
                }

              }
              .infos-right{
                .xflex(2);
                .xdisplayflex();
                align-items: center;
                i{
                  margin-left: 5/32rem;
                }
              }
              .infos-right:hover{
                color: @textcolor;
                cursor: pointer;
              }
            }
          }
        }
        .more{
          span{
            .xdisplayflex();
            align-items: center;
            justify-content: center;
            font-size: 16/32rem;
            color: @color;
            margin-bottom: 26/32rem;
          }
        }
      }
    }
  }
  @media (min-width: 750px) {// PC端
  .home{
    .index{
      .home-title{
        text-align: left;
        margin-bottom: 20px;
        .h2()
      }
      ul{
        li{
          margin-bottom: 26px;
          .content{
            .xdisplayflex();
            flex-wrap: wrap;
            -webkit-flex-wrap: wrap;
            -ms-flex-wrap: wrap;
            margin-bottom: 30px;
            .imgs{
              .xflex(3);
              margin-right: 20px;
              img{
                width: 240px;
                height: 145px;
              }
              img:hover{
                cursor: pointer;
              }
            }
            .text{
              .xflex(7);
              text-align: left;
              .text-top{
                margin-bottom: 10px;
                i{
                  color: #fff;
                  font-size: 15px;
                  background: @textcolor;
                  padding: 3px 10px;
                  -webkit-border-top-right-radius: 50%;
                  -webkit-border-bottom-right-radius: 50%;
                  border-top-right-radius:50%;
                  border-bottom-right-radius:50%;
                }
                span{
                  font-size: 20px;
                }
                span:hover{
                  color: @textcolor;
                  cursor: pointer;
                }
              }
              .text-content:hover{
                cursor: pointer;
              }
              p{
                font-size: 15px;
              }
            }
          }
          .infos{
            .xdisplayflex();
            color: @color;
            font-size: 13px;
            .infos-left{
              .xflex(8.5);
              .xdisplayflex();
              align-items: center;
              div{
                .xdisplayflex();
                align-items: center;
                margin-right: 10px;
                i{
                  margin: 0 3px;
                }
                i:first-child{
                  margin-left: 0;
                }
              }

            }
            .infos-right{
              .xflex(1.5);
              .xdisplayflex();
              align-items: center;
              i{
                margin-left: 5px;
              }
            }
            .infos-right:hover{
              color: @textcolor;
              cursor: pointer;
            }
          }
        }
      }
      .more{
        span{
          .xdisplayflex();
          align-items: center;
          justify-content: center;
          font-size: 16px;
          color: @color;
        }
        span:hover{
          color: @textcolor;
          cursor: pointer;
        }
      }
    }
  }
  }
</style>



