<script>
export default {
  name: 'Home',
  data(){
    return{
      header:{
        // author_img:require("./assets/img/author.jpg")
        nav_link:[],
        name:"",
        dec:""
      },
      body:{
        leader:[],
        links:[],
        time:{
          year:"",
          month:"",
          date:"",
          hour:"",
          minutes:"",
          seconds:""
        },
        info:{},
        motto:""
      },
      login:{
        status: false,
        signIn:{
          name:"",
          password:"",
          identity:"游客",
          status: false,
        },
        registered:{
          name:"",
          email:"",
          password:"",
          again:"",
          status: false,
        },
        getPwssword:{
          name:"",
          status: false,
        }
      },
      showNav:false,
      iphone:{
        nav:false,
        status:false,
        pStyle:{
          fontSize: '16px',
          background: 'rgba(200, 200, 200,0.3)',
          margin: '2px 0',
        }
      }
    }
  },
  created(){
  },
  mounted(){
    setInterval(this.getTime, 1000);
    this.isIe();
    this.getWidth();
    this.getConfig();
  },
  methods:{
    getTime(){
      let date = new Date();
      this.body.time.year = date.getFullYear();
      this.body.time.month = date.getMonth() + 1;
      this.body.time.date = date.getDate();
      this.body.time.hour = date.getHours();
      this.body.time.minutes = date.getMinutes();
      this.body.time.seconds = date.getSeconds();
      if (this.body.time.month >= 1 && this.body.time.month <= 9){
        this.body.time.month = "0" + this.body.time.month;
      }
      if (this.body.time.date >= 0 && this.body.time.date <= 9) {
        this.body.time.date = "0" + this.body.time.date;
      }
      if (this.body.time.seconds >= 0 && this.body.time.seconds <= 9) {
        this.body.time.seconds = "0" + this.body.time.seconds;
      }
    },
    isIe(){
      if(navigator.userAgent.indexOf("MSIE 6.0")>0){
        alert("ie浏览器版本过低,请更换成高版本浏览器,如ie9+");
      }else if(navigator.userAgent.indexOf("MSIE 7.0")>0){
        alert("ie浏览器版本过低,请更换成高版本浏览器,如ie9+");
      }else if(navigator.userAgent.indexOf("MSIE 8.0")>0){
        alert("ie浏览器版本过低,请更换成高版本浏览器,如ie9+");
      }
    },
    showLogin(flag){
      this.login.status = true;
      this.iphone.nav = false;
      let w = window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth;
      if(w<750){// 移动端 控制隐藏其他组件 显示登录菜单
        this.iphone.status=true;
      }
      if(flag){
        this.login.signIn.status = true;
        this.login.registered.status = false;
        this.login.getPwssword.status = false
      }else {
        this.login.signIn.status = false;
        this.login.getPwssword.status = false;
        this.login.registered.status = true
      }
      if(flag === 2){
        this.login.signIn.status = false;
        this.login.getPwssword.status = true;
        this.login.registered.status = false
      }
    },
    displayLogin(){
      this.login.status = false;
      let w = window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth;
      if(w<750){ // 移动端 控制隐藏其他组件 显示登录菜单
        this.iphone.status = !this.iphone.status;
      }
    },
    tabColor(val){
      if(val.path === '/web'){
        this.header.name = 'web前端';
        this.header.dec = '内容';
      }else if(val.path === '/golang'){
        this.header.name = '后端Golang';
        this.header.dec = '内容';
      }else if(val.path === '/python'){
        this.header.name = '后端Python';
        this.header.dec = '内容';
      }else if(val.path === '/about'){
        this.header.name = '关于作者';
        this.header.dec = '内容';
      }else{
        this.header.name = '听风看海';
        this.header.dec = '个人博客';
      }
    },
    getWidth(){
      let w = window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth;
      if(w>750){
        this.showNav=true;
      }
    },
    getConfig(){
      this.$http.get("../static/config/init.json",{}).then(res => {
        this.header = res.data.header;
        this.body = res.data.body;
      },error=>{
        this.$Notice.error({
          title: '错误信息',
          desc: 'get init.json file error!'
        });
      })
    }
  }
}
</script>

