          input{
            font-size: 13/32rem;
            width: 100%;
            height: 60/32rem;
            margin: 15/32rem 0;
            padding-left: 5/32rem;
            border: 1/32rem solid rgba(102, 102, 102, 0.5);
            border-radius: 3/32rem;
          }
          button{
            background: rgba(120, 191, 254, 1);
            width: 100%;
            height: 40/32rem;
            border-radius: 3/32rem;
            color: #fff;
            border:0;
            cursor: pointer;
            margin: 5/32rem 0;
          }
        }
        .bom{
          .xdisplayflex();
          justify-content: center;
          font-size: 13.5px;
          margin-bottom: 20px;
          span{
            margin: 0 10px;
            cursor: pointer;
          }
          span:hover{
            color: #00a2ff;
          }
        }
      }
    }
  }
}
@media (min-width: 750px) {// PC端
  #app{
    header{
      width: 100%;
      height: 300px;
      text-align: center;
      .bgimg("./assets/img/header-bg.jpg");
      .nav{
        width: 100%;
        height: 97px;
        .nav-content{
          width: 63%;
          height: 100%;
          margin: 0 auto;
          .xdisplayflex();
          .nav-content-left{
            .xflex(2);
            .xdisplayflex();
            align-items: center;
            text-align: left;
            div{
              .bgimg("./assets/img/logo.png");
              height: 50px;
              width: 50px;
              // border: 1px solid rgb(146, 192, 238);
              /*border-radius: 50%;*/
            }
            // border: 2px solid salmon;
          }
          .nav-content-right{
            .xflex(8);
            // border: 2px solid salmon;
            font-size: 18px;
            .xdisplayflex();
            align-items: center;
            justify-content: flex-end;
            text-align: right;
            a{
              padding: 0 10px;
              color: #fff;
            }
            a:hover{
              .xboxshow();
            }
          }
        }
      }
      .nav-animation{
        margin-top: 30px;
        animation: showText 3s;
        -webkit-animation: showText 3s;
        -o-animation: showText 3s;
        h2{
          font-size: 36px;
          font-family: 'Avenir', Helvetica, Arial, sans-serif;
          -webkit-font-smoothing: antialiased;
          -moz-osx-font-smoothing: grayscale;
          color: #fff;
          margin-bottom: 18px;
        }
        span{
          font-size: 23px;
          font-family: 'Avenir', Helvetica, Arial, sans-serif;
          -webkit-font-smoothing: antialiased;
          -moz-osx-font-smoothing: grayscale;
          color: #fff;
        }
      }
    }
    .conent-show{
      width: 63%;
      margin: 36px auto;
      .xdisplayflex();
      section{
        .xflex(7);
        margin-right: 20px;
      }
      aside{
        /*border: 1px solid #2c3e50;*/
        .xflex(3);
        margin-left: 20px;
        .user-info{
          /*border: 1px solid rgba(0, 0, 0, 0.61);*/
          margin-bottom: 36px;
          h3{
            color: rgba(83, 83, 83, 0.6);
            font-size: 16px;
          }
          .btn{
            margin: 16px 0;
            button{
              background: #ffffff;
              cursor: pointer;
              padding: 6px 10px;
              margin: 0 5px;
              color: rgba(0, 0, 0, 0.61);
              border: 1px solid rgba(83, 83, 83, 0.24);
            }
            button:hover{
              color: #0097ee;
            }
          }
        }
        .current-time{
          text-align: left;
          margin-bottom: 36px;
          .h2();
          .show-times{
            width: 70%;
            text-align: center;
            margin: 16px auto;
            font-size: 16px;
            color: #fff;
            background: #222;
          }
        }
        .author-info{
          text-align: left;
          margin-bottom: 36px;
          .avatar{
            height: 100px;
            .bgimg("./assets/img/banner.png");
            text-align: center;
            img{
              height: 100px;
              width: 100px;
              border-radius: 50%;
              margin-top: 50px;
            }
          }
          ul{
            margin-top: 58px;
            margin-left: 5px;
            li{
              margin: 10px 0;
              .xdisplayflex();
              align-items: center;
              i{
                font-size: 18px;
                margin-right: 10px;
                color: rgb(102, 102, 102);
              }
              a{
                font-size: 13/32rem;
                color: rgb(102, 102, 102);
              }
              span{
                font-size: 13px;
                color: rgb(102, 102, 102);
              }
            }
          }
          .abtext{
            color: rgba(102, 102, 102, 0.6);
            font-size: 14.5px;
            margin-left: 5px;
          }
        }
        .music{
          text-align: left;
          margin-bottom: 36px;
          .h2();
          position: relative;
          i{
            position: absolute;
            z-index: 999;
            font-size: 18px;
            opacity: 0;
          }
          i:nth-child(2){
            top: 0;
            left: 16%;
            -webkit-animation: musics 5s infinite 2.5s;
            -o-animation: musics 5s infinite  2.5s;
            animation: musics 5s infinite  2.5s;
          }
          i:nth-child(3){
            top: 0;
            left: 23%;
            -webkit-animation: musics 9s infinite;
            -o-animation: musics 9s infinite;
            animation: musics 9s infinite;
          }i:nth-child(4){
            top: 0;
            left: 30%;
            -webkit-animation: musics 7s infinite 5s;
            -o-animation: musics 7s infinite  5s;
            animation: musics 7s infinite  5s;
          }
          .music-content{
            margin: 5px 0;
            text-align: center;
          }
        }
        .leader-board{
          text-align: left;
          margin-bottom: 36px;
          .h2();
          ul{
            margin-left: 5px;
            li{
              margin: 5px 0;
              .xdisplayflex();
              align-items: center;
              i{
                .xdisplayflex();
                align-items: center;
                justify-content: center;
                font-size: 13px;
                width: 18px;
                height: 18px;
                background: rgb(102, 102, 102);
                color: #fff;
                border-radius: 50%;
              }
              a{
                margin-left: 10px;
                font-size: 13px;
                color: rgb(102, 102, 102);
              }
              a:hover{
              color: rgba(0,162,255,0.6);
            }
            }
          }
        }
        .links{
          text-align: left;
          margin-bottom: 36px;
          .h2();
          ul{
            margin-left: 5px;
            .xdisplayflex();
            flex-wrap: wrap;
            -ms-flex-wrap: wrap;
            -webkit-flex-wrap: wrap;
            li{
              margin: 5px 10px;
              align-items: center;
              a{
                font-size: 16px;
                color: rgb(102, 102, 102);
              }
              a:hover{
                color: #00a2ff;
              }
            }
          }
        }
        .reward{
          text-align: left;
          .h2();
          .wx{
            text-align: center;
            background: green;
            img{
              width: 70%;
              height: 70%;
              margin: 10px 0;
            }
          }
          .zfb{
            margin: 10px 0;
            text-align: center;
            background: dodgerblue;
            img{
              width: 70%;
              height: 70%;
              margin: 10px 0;
            }
          }
        }
      }
    }
    footer{
      text-align: center;
      background: #23282D;
      font-size: 13px;
      color: rgba(255,255,255,.3);

    }
    .login{
      position: fixed;
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;
      background: rgba(0,0,0,0.2);
      .xdisplayflex();
      align-items: center;
      justify-content: center;
      .signin{
        width: 400px;
        background: #fff;
        border-radius: 5px;
        color: @color;
        font-family: "Microsoft Yahei UI","Microsoft Yahei",sans-serif;
        .header{
          .xdisplayflex();
          height: 40px;
          justify-content: center;
          margin-bottom: 20px;
          span{
            font-size: 20px;
            .xdisplayflex();
            justify-content: center;
            align-items: center;
            .xflex(8.8);
          }
          i{
            font-size: 23px;
            .xdisplayflex();
            justify-content: center;
            align-items: center;
            .xflex(1.2);
            cursor: pointer;
          }
          i:hover{
            color: #00a2ff;
          }
        }
        .signin-content{
          margin: 0 50px;
          text-align: left;
          font-size: 15px;
          input{
            font-size: 13px;
            width: 100%;
            height: 33px;
            margin: 15px 0;
            padding-left: 5px;
            border: 1px solid rgba(102, 102, 102, 0.5);
            border-radius: 3px;
          }
          button{
            background: rgba(120, 191, 254, 1);
            width: 100%;
            height: 40px;
            border-radius: 3px;
            color: #fff;
            border:0px;
            cursor: pointer;
            margin: 5px 0;
          }
        }
        .bom{
          .xdisplayflex();
          justify-content: center;
          font-size: 13.5px;
          margin-bottom: 20px;
          span{
            margin: 0 10px;
            cursor: pointer;
          }
          span:hover{
            color: #00a2ff;
          }
        }
      }
    }
  }
}
</style>



