import Vue from 'vue'
import Router from 'vue-router'
import home from '@/components/home'

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: home
    },{
      path: '/web',
      name: 'Web',
      component: resolve => require(['@/components/web'],resolve)
    },{
      path: '/golang',
      name: 'Golang',
      component: resolve => require(['@/components/golang'],resolve)
    },{
      path: '/python',
      name: 'Python',
      component: resolve => require(['@/components/python'],resolve)
    },{
      path: '/about',
      name: 'About',
      component: resolve => require(['@/components/aboutme'],resolve)
    },
    {
      path: '/',
      redirect: '/' //重定向到登录页面
    }
  ]
})



